
div.mainpage-root{
  min-height: 100%;
  opacity: 0;
  -webkit-opacity: 0;
  -moz-opacity: 0;
  transition: opacity 0.5s;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
div.mainpage-root.show{
  -webkit-opacity: 1;
  -moz-opacity: 1;
  opacity: 1;
}
div.mainpage-header{
  position: relative;
  /*height:10rem;*/
}
div.mainpage-header>h1{
  display: block;
  position: absolute;
  font-size: 0;
  background: url("../assets/mainpage/title.png") no-repeat center;
  background-size: auto 100%;
  height:3rem;
  width:6.5rem;
  margin:1.2rem auto;
  left:0;
  right:0;
  top:0;
  transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
}
div.mainpage-header>div.search{
  display: block;
  position: absolute;
  height:1.5rem;
  top:1.450rem;
  right:0;
  padding-right:0.5rem;
}
div.mainpage-header.active>h1{
  transform: translateX(calc(1rem - 100%))scale(0.9);
  -moz-transform: translateX(calc(1rem - 100%))scale(0.9);
  -webkit-transform: translateX(calc(1rem - 100%))scale(0.9);
}
div.mainpage-header>div>button{
  display: inline-block;
  height:1rem;
  width: 1rem;
  border:0;
  font-size: 0;
  background: transparent url("../assets/mainpage/search.png");
  background-size:auto 100%;
  padding:0;
  line-height: 1.5rem;
}
div.mainpage-header>div>input{
  border:0;
  height:1.5rem;
  border-radius: 0.75rem;
  box-sizing: border-box;
  vertical-align: middle;
  background-color: rgb(227,227,229);
  transform: scale(0);
  -moz-transform: scale(0);
  -webkit-transform: scale(0);
  width:1.5rem;
  transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  margin:0 0.25rem 0 0;
  padding:0 0.5rem;
}
div.mainpage-header.active>div>input{
  width:9rem;
  transform: scale(1);
  -moz-transform: scale(1);
  -webkit-transform: scale(1);
}
div.mainpage-body{
  width:100%;
  -ms-flex-positive: 1;
      flex-grow: 1;
}
div.mainpage-body div.img-div{
  background:url("../assets/mainpage/columns.png");
  height:205px;
  width:181px;
  transform-origin: 0 0;
  z-index: 11;
  position: relative;
}
div.mainpage-body h3{
  font-size: 0.8rem;
  position:absolute;
  bottom:0.5rem;
  text-align: center;
  width:5rem;
  color:#FFF;
  z-index: 11;
  font-weight: 400;
}
div.mainpage-body div.column{
  width:100%;
  text-align: center;
  font-size: 0;
  margin-bottom: 0.5rem;
}
div.mainpage-body div.column-block{
  height:5.663rem;
  width:5rem;
  display: inline-block;
  position: relative;
  padding:0.5rem;
  border:none;
}
div.topic>p::after{
  content: "";
  background: url("../assets/mainpage/topic_right.png");
  height:0.7rem;
  display: inline-block;
  width:0.7rem;
  background-size: 100% 100%;
  margin:0 0 0.2rem 0.2rem;
}
div.topic>p::before{
  content: "";
  background: url("../assets/mainpage/topic_left.png");
  height:0.7rem;
  display: inline-block;
  width:0.7rem;
  background-size: 100% 100%;
  margin:0  0.2rem 0.2rem 0;
}
div.topic>p{
  font-size:0.7rem;
  text-align: center;
  padding:0;
  margin:0 auto;
}
div.topic{
  margin:0 auto;
  position: absolute;
  bottom:1rem;
  width:100%;
}
div.mainpage-root h2::before{
  content: "";
  width: 1rem;
  height: 0.2rem;
  margin:0.25rem;
  background: url("../assets/mainpage/left.png") no-repeat;
  background-size: 100%;
  display: inline-block;
}
div.mainpage-root h2::after{
  content: "";
  width: 1rem;
  height: 0.2rem;
  margin:0.25rem;
  background: url("../assets/mainpage/left.png") no-repeat;
  background-size: 100%;
  display: inline-block;
  transform: rotate(180deg);
}
div.mainpage-root h2{
  text-align: center;
  margin:0 0 0.6rem 0;
  padding:0;
  font-weight: 300;
  font-size: 1rem;
}
div.mainpage-body div.column-block>*{
  transition: all 0.5s ease-in-out;
  opacity: 1;
  -moz-opacity: 1;
  -webkit-opacity: 1;
}
div.mainpage-body div.column-block{
  border-radius:0.5rem;
}
div.mainpage-body div.column-block.active{
}
div.mainpage-body div.column-block.active>div.img-div{
  opacity: 0;
  -webkit-opacity: 0;
  -moz-opacity: 0;
  z-index: 7;
}
div.mainpage-body div.column-block.active>h3{
  opacity: 0;
  -webkit-opacity: 0;
  -moz-opacity: 0;
  z-index: 7;
}
div.mainpage-body div.column-block.active>div.back{
  transform-origin: 50% 50%;
  transform: scale(1.2);
}
div.column-block div.back{
  width:5rem;
  height:5.663rem;
  position: absolute;
  bottom: 0.5rem;
  z-index: 8;
}
div.column-block div.back a{
  text-decoration: none;
  color: #FFF;
}
div.column-block div.back>div.img-div-a{
  background:url("../assets/mainpage/columns.png");
  height:205px;
  width:181px;
  transform-origin: 0 0;
  position: absolute;
  top:0;
}
div.column-block.active div.back ul{
  display: -ms-flexbox;
  display: flex;
}
div.column-block div.back ul{
  list-style: none;
  padding:0.5rem;
  margin: 0;
  width:100%;
  height:100%;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  display: none;
}
div.column-block div.back li{
  font-size: 0.7rem;
  color:#FFF;
  position: relative;
  z-index: 10;
  width:2rem;
  margin:0.35rem 0;
  text-align: left;
}
div.hot-store img{
  width:100%;
  height:auto;
}
div.hot-store>div{
  position: relative;
}
div.hot-store{
  /*position: absolute;
  bottom:0;*/
  width:100%;
  position: relative;
}
div.hot-store div.back{
  width:100%;
  padding:24% 0 0 0;
  background: url("../assets/mainpage/bottom.png");
  background-size: 100% 100%;
}
div.hot-store div.mainpage-bottom{
  width:100%;
  height:100% ;
  position: absolute;
  top:0;
  padding:0.5rem;
  margin:0;
  box-sizing: border-box;
  text-align: center;
}
div.hot-store div.mainpage-bottom>a:focus{
  background: #FFFFFF;
  color:#6c77c9;
  border:transparent 1px solid;
}
div.hot-store div.mainpage-bottom>a{
  display:inline-block;
  border: solid 1px #FFF;
  border-radius: 0.5rem;
  width:3rem;
  height:1.2rem;
  font-size: 0.7rem;
  color:#FFF;
  text-align: center;
  line-height: 1.2rem;
  margin: 0.3rem 0.5rem;
  text-decoration: none;
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
}



