
div.mark-root{
  opacity:0;
  transition: all 0.5s;
}
div.mark-root.show{
  opacity: 1;
}
div.mark-header>span{
  vertical-align:top;
  line-height: 1rem;
  margin:1rem 0;
  display: inline-block;
  height:1rem;
  color:#555792;
}
div.mark-header{
  height:3rem;
}
div.mark-header>a{
  display: inline-block;
  width:0.55rem;
  height:1rem;

  margin:1rem;
}
div.mark-header>a>span{
  background: url("../assets/storeList/return_blue.png");
  background-size: 100% 100%;
  display: inline-block;
  width:100%;
  height:100%;
}
div.mark-body{
  width:100%;
  padding:1rem;
  box-sizing: border-box;
  position: relative;
}
div.mark-body>div.img{
  width:100%;
  padding:70% 2rem 0 2rem;
  box-sizing: border-box;
  height:10rem;
  position: relative;
}
div.mark-body>div.img>img{
  width:calc(100% - 4rem);
  height:auto;
  position: absolute;
  bottom:0;
  margin:0 auto;
}
div.mark-footer>{
  height:5rem;
  padding:1rem 0;
  box-sizing: border-box;
}
div.switcher{
  width:100%;
  margin-top: 2rem;
}
div.switcher h2{
  text-align: center;
  margin: 0;
  font-size: 1.2rem;
  color:#564BA3;
}
div.switcher div{
  width:100%;
}
div.switcher span{
  display: inline-block;
  width:1rem;
  height:1rem;
}
div.switcher>div{
  font-size: 0;
}
div.switcher>div>span:last-child{
  text-align: end;
}
div.switcher>div>span:first-child{
  text-align: left;
}
div.switcher>div>span{
  display: inline-block;
  font-size: 0.9rem;
  color: #AAAAAA;
  width: 1.5rem;
  text-align: center;
  vertical-align: middle;
  line-height: 0.9rem;
}
div.switcher>div>div>div{
  width:1px;
  height:0.15rem;
}
div.switcher div.button{
  background: url("../assets/mark/button_mark.png") no-repeat center center;
  width:1rem;
  height:1rem;
  border-radius: 0.5rem;
  position: absolute;
  top:-0.4rem;
  background-size:200% 200%;
  left: -0.5rem;
}
div.switcher>div>div{
  width:calc(100% - 3rem);
  height:0.15rem;
  border-radius:0.7rem;
  padding:0 0.5rem;
  box-sizing: border-box;
  display: inline-block;
  vertical-align: middle;
  background: none repeat scroll 0 0 #574DA3;
  background-clip: content-box;
  margin:0 0;
  position: relative;
}
div.mark-footer{
  bottom:0;
  padding: 2rem 0 3rem 0;
  box-sizing: border-box;
  width: 100%;
}
div.buttons{
  text-align: center;
  height: 2rem;
  margin:0 auto;
  width:100%;
}
div.buttons>button:first-child{
  background: url("../assets/mark/wait_mark.png") no-repeat;
  background-size: 100% 100%;
  margin-right:1rem;
}
div.buttons>button:last-child{
  background: url("../assets/mark/finished_mark.png") no-repeat;
  background-size: 100% 100%;
}
div.buttons>button{

  border: 0;
  border-radius: 1rem;
  height:2rem;
  width:7.46rem;
  display: inline-block;
  color:#FFF;
  font-size: 0.8rem;
  box-shadow: 0 0 0.8rem 0.2rem rgba(84,101,157,0.2);
}
div.alert.show{
  transform: translateY(0);
  background-color: red;
  opacity: 1;
}
div.alert>p{
  text-align: center;
  line-height: 3rem;
  font-size: 0.8rem;
  margin:0;
  color: #FFF;
}
div.alert{
  height: 3rem;
  width:100%;
  position: fixed;
  top:0;
  transform: translateY(-100%);
  transition: all 0.5s;
  z-index: 100;
  opacity: 0;
}



